<template>
    <div class="container">
        <Title greeting-message="教學品保"></Title>
        <b-nav>
            <b-nav-item href="#" @click="updatePage(1)">組織架構</b-nav-item>
            <b-nav-item href="#" @click="updatePage(2)">品保系統</b-nav-item>
            <b-nav-item href="#" @click="updatePage(3)">品保概要</b-nav-item>
            <b-nav-item href="#" @click="updatePage(4)">課程與教學</b-nav-item>
            <b-nav-item href="#" @click="updatePage(5)">學生學習與成效</b-nav-item>
        </b-nav>
        <div class="vue-html" v-html="news.context"></div>
        <div class="text-center">
            <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
        </div>
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import { NewsServer } from "../../providers/http-server.js";
import moment from 'moment';
let page = 1;

export default {
    name: 'App',
    data() {
        return {
            news: []
        }
    },
    components: { 
        Title
    },
    created(){
        this.moment = moment;
        NewsServer.item(page).then(
            (response) => {
                this.news = response.data;
                this.news_type = response.data.notification.type;
            }
        )
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        },
        updatePage(page) {
            NewsServer.item(page).then(
            (response) => {
                this.news = response.data;
                this.news_type = response.data.notification.type;
            }
        )
        }
    }
};
</script>

<style scoped>
@import '../../libs/ckeditor_plugin.css';
</style>